a {
    color: $text-color;
    text-decoration: none;
    -webkit-text-fill-color: currentColor;

    &:hover {
        color: $theme-color;
        text-decoration: none;
    }
}

article a:not(.media-card-link):not(.media-card-small a), .news a {
    color: $theme-color;
    -webkit-text-fill-color: $theme-color;
    font-weight: 400;

    &:hover {
        text-decoration: underline;
    }
}

.post-content a:not(.media-card-link),
.page-content a:not(.person-card a):not(.media-card-link) {
    color: $theme-color;
    -webkit-text-fill-color: $theme-color;
}

.media-card-small a,
.media-card a {
    color: inherit;
    -webkit-text-fill-color: inherit;
    font-weight: inherit;
}

.social a {
    color: $text-color;

    &:hover { color: $theme-color; }
}


